body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.title {
  font-family: "Open Sans", sans-serif;
  padding-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

.card {
  background-color: #ffffff;
  border-radius: 8px;
  border: #ffa630;
  border-style: 5px;
  font-weight: 15px;
  width: 100%;
  display: inline-block;
  align-content: center;
}

span .card-title {
  font-family: "Roboto", sans-serif;
  font-size: 50px;
  text-decoration: underline;
}

li {
  font-family: "Open Sans", sans-serif;
  padding: 3.5px;
  font-size: 14px;
}

span {
  font-weight: bold;
}

.row {
  width: 100%;
  display: inline-block;
  margin: auto;
}

.wholecard {
  padding-left: 120px;
}

.rstlist {
  float: right;
}

p {
  font-family: "Qwigley", cursive;
  font-size: 45px;
  padding-top: 40px;
  margin-right: 10px;
  margin: 20px;
  text-align: right;
}

@media screen and (max-width: 1100px) {
  p {
    display: none;
  }
}

.collection {
  font-family: "Open Sans", sans-serif;
}

@media only screen and (max-width: 400px) {
  .collection {
    font-family: "Open Sans", sans-serif;
  }
}

nav {
  background-color: #074f57;
}

.nav-title {
  font-family: "Open Sans", sans-serif;
}

ul {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  padding-right: 10px;
  border: 5px;
}

@media screen and (max-width: 600px) {
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

